<template>
  <Container :title="title">
    <v-form @submit="submit">
      <template slot="base">
        <group :label="lang.name" type="input" v-model="auth_group.name" />
        <group :label="lang.module" type="input" v-model="auth_group.module" />
        <group :label="lang.level" type="input" v-model="auth_group.level" />
        <group :label="lang.description" type="input" v-model="auth_group.description" />
        <group :label="lang.status" type="status" v-model="auth_group.status" />
        <group :label="lang.rules" >
          <template v-for="vol in rules" >
            <p style="background: #f9f9f9; padding: 8px; clear: both; margin-bottom: 0">
               <checkbox :value="vol.id" v-model="rule_checkbox" />  {{vol.name}}
            </p>
            <div v-if="vol.children">
              <template v-for="(item,index) in vol.children">
                <p style="margin-left: 30px; float: left; margin-bottom: 0; padding: 10px 0">
                  <checkbox :value="item.id" v-model="rule_checkbox" />  {{item.name}}</p>
                <br style="clear: both" v-if="(index+1)%5 === 0">
              </template>
            </div>
          </template>
        </group>
      </template>
    </v-form>
  </Container>
</template>

<script>
  import {Container,VForm,Group, Checkbox} from "@/components"

  export default {
    components: {Checkbox, Group, VForm,  Container },
    data(){
      return {
        title:this.$t('auth_group.editTitle'),
        lang:this.$t('auth_group'),
      }
    },
    computed:{
      auth_group:{
        set(value){
          this.$store.commit('user/SET_AUTH_GROUP', value)
        },
        get(){
          return this.$store.state.user.auth_group
        }
      },
      rule_checkbox:{
        set(value){
          this.$store.commit('user/SET_RULE_CHECKBOX', value)
        },
        get(){
          return this.$store.state.user.rule_checkbox
        }
      },
      rules(){
        return this.$store.state.user.rule_tree
      }
    },
    created(){
      const uuid = this.$route.query['id']
      this.$store.dispatch('user/groupInitial',uuid)
    },
    methods:{
      submit(){
        this.auth_group.rule_array = this.rule_checkbox

        this.$store.dispatch('user/editGroup').then( resp =>{
          if (resp){
            this.$toast.success({
              title:'API 请求成功',
              message: resp.error
            })
          }
        })
      }
    }
  }
</script>



